.container {
  width: 400px;
  height: 570px;
  margin: 15px auto;
  border-radius: 10px;
  background-image: linear-gradient(120deg, #bcc9fe 0%, #3865e8 100%);
}
.container .item {
  width: 350px;
  margin: 5px auto;
}

.container .top-bar {
  display: flex;
  justify-content: center;
  gap: 5px;
  padding-top: 30px;
  width: 100%;
}

.container .top-bar input {
  display: flex;

  border: none;
  outline: none;
  border-radius: 50px;

  color: #000;
  font-size: 20px;
  font-weight: 400;
  padding: 10px;
}

.container .top-bar .search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  background: white;
  border-radius: 40px;
  cursor: pointer;
}

.container .top-bar .search-icon:hover {
  background-color: #6f8ff1;
  transition: 0.4s;
}

.weather-img {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  margin: 10px auto;
}

.weather-img img {
  width: 130px;
  height: 130px;
}

.weather-temp {
  color: white;
  font-size: 60px;
  font-weight: 600;
}

.state {
  font-size: 30px;

  color: white;
  letter-spacing: 5px;
}

.town {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 60px;
  font-weight: 600;
  width: 100%;
}

.data-container {
  color: white;
  display: flex;
  margin: 50px auto;
  width: 300px;
  width: 90%;
}

.element {
  margin: 20px auto;
  display: flex;
  align-items: center;
}

.data,
.text {
  font-size: 19px;
  font-weight: 700;
}

.data {
  margin: 4px;
}

.data .text {
  color: #ffc16a;
}

.err {
  color: rgb(255, 79, 79);
  font-size: 20px;
  font-weight: 600;
}
